// Tabs
// -------------------------

// Give the tabs something to sit on
.nav-tabs {
  border-bottom: 2px solid @nav-tabs-border-color; // #ddd
  > li {
    float: left;
    // Make the list-items overlay the bottom border
    margin-bottom: -2px;

    // Actual tabs (as links)
    > a {
      width: @nav-tabs-btn-width;
      text-align: center;
      margin-right: 0;
      color: #666;
      // line-height: @line-height-base;
      border: none;
      border-bottom: 4px solid transparent;
      border-radius: 0;   //@border-radius-base @border-radius-base 0 0;
      &:hover {
        border-color: transparent;
        border-bottom-color: #ccc; //@nav-tabs-link-hover-border-color @nav-tabs-link-hover-border-color @nav-tabs-border-color;
        background-color: #fff;
      }
    }

    // Active state, and its :hover to override normal :hover
    &.active > a {
      &,
      &:hover,
      &:focus {
        color: #666;
        border: none;
        border-bottom: 4px solid @nav-tabs-active-link-hover-border-color;
        cursor: default;
      }
    }
  }
}



.tab-content{
  padding: 14px 0;
}